<template>

<div v-if="!cookieSet" id="cookieConsent">
  <div v-html="message" id="cookieMessage"></div>
  <button type="button" v-on:click="setCookie">{{ buttonText }}</button>
</div>

</template>

<script>

export default {
  name: "CookieConsent",
  data() {
    return {
      cookieSet: false,
      buttonText: "Einverstanden",
      message: "Cookies erleichtern die Bereitstellung unserer Dienste. " +
          "Mit der Nutzung unserer Dienste bist du damit einverstanden, dass wir Cookies verwenden. " +
          "<a href='/datenschutz' style='text-decoration: underline;'>Mehr Infos</a>",
    }
  },
  beforeMount() {
    this.cookieSet = (document.cookie === "cookie-consent=true")
  },
  methods: {
    setCookie: function() {
      // TODO: set an expiration date

      let date = new Date();
      date.setTime(date.getTime() + (100 * 24 * 60 * 60 * 1000));
      const expires = "expires=" + date.toUTCString() + "; ";
      document.cookie = "cookie-consent=true; " + expires + "path=/";
      //document.cookie = "cookie-consent=true; path=/;"
      document.getElementById("cookieConsent").style.display = "none";
    },
  }
}

</script>

<style scoped>

#cookieConsent {
  display: flex;
  justify-content: space-between;
  align-content: center;
  position: fixed;
  width: 100%;
  bottom: 0;
  font-size: 18px;
  background-color: #b5aba7;
}

#cookieConsent #cookieMessage {
  padding: 20px;
  line-height: 1.5;
}

#cookieConsent button {
  font-family: var(--main-website-font-family);
  height: max-content;
  margin: auto 20px auto 0;
  border: none;
  padding: 10px 40px;
  font-size: 18px;
  font-weight: bold;
  background-color: #f2eeea;
  cursor: pointer;
}

#cookieConsent button:hover {
  background-color: #e0dbd5;
}

@media screen and (max-width: 769px) {

  #cookieConsent {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 20px;
  }

  #cookieConsent button {
    margin: 0 auto;
  }

}

</style>