import Vue from 'vue'                                             // Vue
import App from './App.vue'                                       // App Entrypoint

import VueRouter from 'vue-router'                                // Vue Router
import router from "./router"                                     // Router Pages

Vue.use(VueRouter);                                               // Register Router

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
