<template>
  <div id="vimeoWholeWrapper">
    <div id="leftVideo" class="vidBox">
      <div id="vimeoSvgWrapper">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-on:click="playVideo($event, video.imgID, video.iframeID)">
        <!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. -->
        <path fill="white" d="M512 256C512 397.4 397.4 512 256 512C114.6 512 0 397.4 0 256C0 114.6 114.6 0 256 0C397.4 0 512 114.6 512 256zM176 168V344C176 352.7 180.7 360.7 188.3 364.9C195.8 369.2 205.1 369 212.5 364.5L356.5 276.5C363.6 272.1 368 264.4 368 256C368 247.6 363.6 239.9 356.5 235.5L212.5 147.5C205.1 142.1 195.8 142.8 188.3 147.1C180.7 151.3 176 159.3 176 168V168z"/>
        </svg>
      </div>
      <img :id="video.imgID" :src="video.thumbnail" />
      <iframe :id="video.iframeID" :title="video.title" :src="video.src" :allow="video.allow"></iframe>
    </div>
    <div class="videoDescriptionWrapper">
      <div class="videoTitle">{{ video.title }}</div>
      <div class="videoSubtitle">{{ video.subtitle }}</div>
    </div>
  </div>
</template>

<script>

export default {
  name: "VidBoxVimeo",
  props: [ "video" ],
  methods: {
    playVideo: function(event, imgID, iframeID) {
      event.target.style.display = "none";
      document.getElementById(imgID).style.display = "none";
      let iframe = document.getElementById(iframeID);
      // eslint-disable-next-line no-undef
      let player = new Vimeo.Player(iframe);
      player.play();
    }
  }
}

</script>

<style scoped>

#vimeoWholeWrapper {
  background-color: white;
}

.vidBox {
  position: relative;
  width: 90%;
  max-width: 1080px;
  margin: 0 auto;
}

.vidBox:before {
  content: "";
  display: block;
  padding-top: 56%; /* 16:9 format */
}

.vidBox iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;;
  left: 0;
  right: 0;
  bottom: 0;
  border: 0;
}

.vidBox img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
  max-width: 1080px;
  max-height: 720px;
  z-index: 15;
  box-shadow:  -5px 0 0 white, 5px 0 0 white, 0 5px 0 white, 0 -5px 0 white;
}

iframe {
  width: 100%;
  margin: 0 auto;
}

#vimeoSvgWrapper {
  width: 100%;
  display: flex;
  margin: 0 auto;
  justify-content: center;
  background-color: #b5aba7;
}

svg {
  position: absolute;
  height: 30%;
  top: 35%;
  cursor: pointer;
  color: white;
  z-index: 20;
}

.videoDescriptionWrapper {
  position: relative;
  margin: 0 auto;
  width: 90%;
  max-width: 1080px;
  padding-top: 20px;
  text-align: center;
  letter-spacing: 1px;
  line-height: 1.2;
}

.videoTitle {
  margin-bottom: 10px;
  font-family: var(--main-title-font-family);
  font-size: 60px;
}

.videoSubtitle {
  font-size: 25px;
  text-transform: uppercase;
}

@media screen and (max-width: 678px) {

  .videoTitle {
    font-size: 40px;
  }

  .videoSubtitle {
    font-size: 20px;
  }

}

@media screen and (max-width: 500px) {

  .videoDescriptionWrapper {
    padding-top: 15px;
  }

}

</style>